@import "../../global.scss";

.works {
  background-color: #3b5065;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: visible;


    h1 {
      font-size: 70px;
      color: whitesmoke;
      margin-top: 90px;

      @include mobile {
        font-size: 50px;
        margin:10px 0px 0px 0px;
        overflow: visible;
      }
    }
   

  ul {
    margin: 55px;
    padding: 0;
    list-style: none;
    display: flex;
    color: whitesmoke;

    @media(max-width:1682px) {
    }
    @include mobile {
      margin: 10px 0;
      flex-wrap: wrap;
      justify-content: center;
      overflow: visible;
    }
  }

  .container {
    width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;

    @include mobile{
      width: 100%;
    }

    .item {
      width: 500px;
      height: 292px;
      border-radius: 20px;
      border: 1px solid #3b5065;
      margin: 10px 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #3b5065;
      position: relative;
      transition: all 0.5s ease;
      cursor: pointer;

    @include mobile{
      width: 261px;
      height: 198px;
    }

      h3 {
        position: absolute;
        font-size: 69px;
      }

      img {
        width: 100%;
        height: 100%;
        //object-fit: cover;
        object-fit: fill;
        z-index: 1;
      }

      &:hover {
        background-color: #d3c7af;
        img {
          opacity: 0.2;
          z-index: 0;
        }
      }
    }
  }
}
